<template lang="html">
  <div :class="{lefter: !done, righter: done } " class="card ui todo" v-rainbow>
    <div>
      <button type="button"@click="deleteTodo(todo)" class="remove">
        <i class="trash alternate icon"></i>
      </button>
      <div class="remove-after"></div>
      <h2>{{todo.title}}</h2>
      <h3>{{todo.project}}</h3>
      <h4 v-if="done">Completed</h4>
      <hr>
      <ul>
        <li v-for="time in todo.times"> {{time}}</li>
      </ul>
      <transition name="slide-fade" mode="out-in">
        <div v-if="!timing && !done" key="begin">
          <!--  v-show="!timing && !done"-->
          <button type="button" @click="startTime" > Begin </button>
        </div>
        <!--   v-show="timing && !done"-->
        <div class="stopwatch" v-if="timing && !done" key="timer" >
          <stopwatch v-on:calculated="calculated"></stopwatch>
          <button type="button" @click="timing = !timing" v-show="timing"> Done For Now </button>
        </div>
      </transition>
      <br>
      <button type="button" @click="completeTodo(todo)" v-show="!done">
        <i class="check circle outline icon"></i>
      </button>
    </div>
    <div v-if="done">
      <h6>Total Time:</h6>
      {{total}}
    </div>
  </div>
</template>

<script>
import Stopwatch from './Stopwatch';

export default {
  props: ['todo'],
  components: {
    Stopwatch
  },
  data() {
    return {
      total: 0,
      timing: false,
      done: false,
      created: true,
      state: 'beginning'
    };
  },
  methods: {
    completeTodo(todo) {
        this.done = true;
        this.$emit('complete-todo', todo);
      },
      calculated(total) {
        this.total = total.sum;
      },
    deleteTodo(todo) {
        this.$emit('delete-todo', todo);
      },

    startTime: function () {
      this.timing = true;
    }
  },
  directives: {
    'rainbow': {
      bind(el, binding, vnode) {
        el.style.backgroundColor = "#" + Math.random().toString().slice(2, 8);
      }
    }
  }
}
</script>

<style lang="css" scoped>
  .todo {
    /* border: 1px solid black; */
    width: 400px !important;
    height: auto;
    margin: 0 auto;
    margin-bottom: 1em;
    padding: 20px;
    display: block;
  }
  /* .lefter {
  }
  .righter {
    right: -30%;
  } */
  .remove {
    float: right;
  }
  .remove-after {
    clear: both;
  }
  .slide-fade-enter-active {
    transition: all .3s ease;
  }
  .slide-fade-leave-active {
    transition: all .3s cubic-bezier(1.0, 0.5, 0.8, 1.0);
  }
  .slide-fade-enter, .slide-fade-leave-to
  /* .slide-fade-leave-active below version 2.1.8 */ {
    transform: translateX(10px);
    opacity: 0;
  }
</style>



